import { deBounce } from "common/utils.js";
export const itemListenerMinxin = {
  data() {
    return {
      bcFunc: null
    };
  },
  mounted() {
    // 
    let deBounceRefresh = deBounce(this.$refs.scroll.refresh, 50);
    this.bcFunc = () => {
      // $bus  scroll
      deBounceRefresh();
    };
  }
};

